  setSearchClaim = (value: string) => {
    this.setState({ searchClaim: value });
  };

  loadsAllClaims = (
    thisSession: SessionPlanInterface,
    level: 1 | 2,
    claim_type: PlansTypeKeys | undefined
  ) => {
    // LOADING CLAIMING OF CURRENT CANDIDATES ================================
    console.log("====================================");
    console.log({ type: this.state.selected_claim_type });
    console.log("====================================");
    this.props.getAllClaims(
      level,
      thisSession._id,
      (status: boolean, msg: string) => {
        this.setLoadingData(status);
        if (!status && msg.length) {
          this.props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      },
      claim_type
    );
  };

  loadsApplications = (application_id: string) => {
    // LOADING CLAIMING OF CURRENT CANDIDATES ================================
    if (
      !this.props.claiming.allApplications.find(
        (item) => item._id === application_id
      )
    ) {
      this.props.getAllApplications(
        application_id,
        (status: boolean, msg: string) => {
          this.setLoading(status);
          if (!status && msg.length) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    }
  };

  loadsController = (file_id: string, claimType: PlansTypeKeys) => {
    if (claimType === PlansTypeKeys.APPLICATION)
      this.loadsApplications(file_id);
    if (claimType === PlansTypeKeys.EXAMINATION) this.loadsAlAnswers(file_id);
  };

  loadsAlAnswers = (answersheet_id: string) => {
    // LOADING CLAIMING OF CURRENT CANDIDATES ================================
    if (
      !this.props.claiming.allAnswer.find(
        (item) => item.answer_sheet_id === answersheet_id
      )
    ) {
      this.props.getAllAnswers(
        answersheet_id,
        (status: boolean, msg: string) => {
          this.setLoading(status);
          if (!status && msg.length) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    }
  };

  componentDidMount = () => {
    // FC_GetAllClaims();

    // LOADING APPLICATION SESSION ====================================
    if (this.props.application_sessions === null) {
      this.props.GetAllSessionPlans(
        (status: boolean, msg: string, data?: SessionPlanInterface[]) => {
          this.setLoading(status);
          if (!status) {
            if (msg.length) {
              this.props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }
            if (data && data.length > 0) {
              this.setDefaultSession(data);
            }
          }
        }
      );
    } else if (this.props.application_sessions.length > 0) {
      this.setDefaultSession(this.props.application_sessions);
    }

    // LOADING DOCUMENT CATEGORIES ==========================
    if (!this.props.documentReducer.documentCategory) {
      this.props.getDocumentCategory();
      this.setLoading(false);
    }

    // LOADING DOCUMENT CLARIFICATIONS ==========================
    if (!this.props.documentReducer.documentSubCategory) {
      this.props.getDocumentSubCategory();
      this.setLoading(false);
    }

    // LOADING DOCUMENT FIELDS ==========================
    if (!this.props.documentReducer.documentFields) {
      this.props.getDocumentFields();
      this.setLoading(false);
    }

    if (this.props.systemPositions === null) {
      this.props.getPositions((status: boolean, msg: string) => {
        this.setLoading(status);
        console.log({ status: status, msg: msg });
        if (msg.length) {
          this.props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else if (this.state.loading) {
      this.setLoading(false);
    }

    if (this.props.claiming.claimReasons === null) {
      this.props.getClaimReasons((status: boolean, msg: string) => {
        this.setLoading(status);
        if (!status) {
          if (msg.length > 0) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      });
    }

    if (
      this.props.courses === null ||
      (this.props.courses.length === 0 && this.state.loading)
    ) {
      this.props.loadCourses(this.setLoading);
      this.setLoading(false);
    } else {
      this.setLoading(false);
    }
  };

  displayClaimTitle = (
    thisClaim: CandidateClaimInterface
  ): string | JSX.Element => {
    let result: string = thisClaim.claiming_on_id;
    if (thisClaim.type_key === PlansTypeKeys.DOCUMENT_VALIDATION) {
      let thisDocs = thisClaim.user.details.filter(
        (item) => item.validation_status !== "waiting"
      );
      result =
        thisDocs.length > 0
          ? adminDisplayClaimTitle(
              this.props.documentReducer,
              thisDocs,
              thisClaim
            )
          : "Document not found";
    }
    if (thisClaim.type_key === PlansTypeKeys.APPLICATION) {
      if (this.props.claiming.allApplications) {
        let thisApplication = this.props.claiming.allApplications.find(
          (item) => item._id === thisClaim.claiming_on_id
        );
        if (
          thisApplication &&
          this.props.claiming.allApplications &&
          this.props.systemPositions
        ) {
          let thisPosition = this.props.systemPositions.find(
            (item) => item.position_id === thisApplication!.position
          );
          if (thisPosition) {
            result = thisPosition.position_title;
          } else {
            result = "Application not found";
          }
        }
      }
    }

    if (
      thisClaim.type_key === PlansTypeKeys.EXAMINATION &&
      this.props.courses &&
      this.props.claiming.allAnswer
    ) {
      let thisAnswer = getCourseIdBySheetId(
        this.props.claiming.allAnswer,
        thisClaim.claiming_on_id
      );
      if (thisAnswer && getCourseById(this.props.courses, thisAnswer.course)) {
        result =
          "Exam of " +
          getCourseById(this.props.courses, thisAnswer.course)!.course_name;
      } else {
        result = "Unknown exam";
      }
    }

    return result;
  };

  onPeriodChange = (period_id: string) => {
    this.setPeriodChanged(true);
    this.props.getAllClaims(
      this.state.claim_level,
      period_id,
      (status: boolean, msg: string) => {
        !status && this.setPeriodChanged(false);
        this.setLoadingData(status);
        if (status === false && msg.length) {
          this.props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      }
    );
  };

  getLastClaimDate = (claim: CandidateClaimInterface): string => {
    let result: string = DATE(claim.createdAt);
    const claimsLength: number = claim.claims.length;
    result = DATE(claim.claims[claimsLength - 1].createdAt);
    return result;
  };

  DATA_TITLES = (DAT_ClaimByStatus: CandidateClaimInterface[]): string[] => {
    let response: string[] = [];
    for (const item of DAT_ClaimByStatus) {
      if (response.find((itm) => itm === item.title) === undefined) {
        response.push(item.title);
      }
    }
    return response;
  };

  FindClaimsNumber = (
    title: string,
    DAT_ClaimByStatus: CandidateClaimInterface[]
  ): number => {
    let count = DAT_ClaimByStatus.filter((itm) => itm.title === title).length;
    return count;
  };

