import axios from "axios";
import React, { Component } from "react";
import { connect } from "react-redux";
import {
  Auth,
  positionsStoreInterface,
  schoolLevelsStoreInterface,
  System,
  setSelectedEducationCategory,
  loadAllPositionsByInstitute,
} from "../../../actions";
import ClaimReportAnalytic from "../../../components/ClaimReportTools/ClaimReportAnalytic";
import Loading from "../../../components/Loading/Loading";
import {
  ClaimStatus,
  education_category,
  PlansTypeKeys,
} from "../../../interfaces";
import { StoreState } from "../../../reducers";
import { capitalizeFirstLetter } from "../../../shared/dataTest";
import { API } from "../../../utils/api";
import { displayNumbers } from "../../../utils/functions";

interface GetClaimsReportInterface {
  _id: string;
  previous: {
    evaluater: {
      email: string;
      name: string;
    };
    reason: string;
    status: string;
  };
  title: string;
  last_claim_status: ClaimStatus;
  claiming_on_id: string;
  type_key: PlansTypeKeys;
  default_claim_resaons: string;
  user: string;
  period_id: string;
  claim_level: number;
  claims: [
    {
      claim_status: ClaimStatus;
      _id: string;
      level: number;
      reason: string;
      date_created: string;
      createdAt: string;
      updatedAt: string;
    }
  ];
  createdAt: string;
  updatedAt: string;
  __v: 0;
}

interface PositionSummaryInterface {
  position_title: string;
  total: number;
  status: ClaimStatus;
}

interface ClaimReportProps {
  auth: Auth;
  system: System;
  positions: positionsStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
  setSelectedEducationCategory: (data: education_category) => void;
  loadAllPositionsByInstitute: (
    institution_id: string,
    callback: (loading: boolean) => void
  ) => void;
}
interface ClaimReportState {
  error: string;
  success: string;
  loading: boolean;
  data: GetClaimsReportInterface[] | null;
  selected_level: number;
  selected_claim_type: PlansTypeKeys;
  view_type: "Table" | "Analytical";
}

class _ClaimReport extends Component<ClaimReportProps, ClaimReportState> {
  constructor(props: ClaimReportProps) {
    super(props);

    this.state = {
      loading: true,
      error: "",
      success: "",
      data: null,
      selected_level: 1,
      selected_claim_type: PlansTypeKeys.APPLICATION,
      view_type: "Table",
    };
  }
  setLoading = (status: boolean) => this.setState({ loading: status });
  GetClaims = async (level: number, session_id: string) => {
    this.setLoading(true);
    try {
      const res = await axios.post<GetClaimsReportInterface[]>(
        `${API.CLAIMING}/user/level/${level}`,
        {
          period_id: session_id,
        }
      );
      if (res.data) {
        this.setState({
          data: res.data,
        });
      }
      console.log("Response: ", res.data);
      this.setLoading(false);
    } catch (error: any) {
      console.log("Error: ", { ...error });
      this.setLoading(false);
    }
  };

  GetSummaryFilter = (data: PositionSummaryInterface[]) => {
    let response: {
      position_title: string;
      total: number;
      pending: number;
      validated: number;
    }[] = [];
    for (const item of data) {
      const test = response.find(
        (itm) => itm.position_title === item.position_title
      );
      if (test !== undefined) {
        response = response.filter(
          (itm) => itm.position_title !== test.position_title
        );
        response.push({
          position_title: test.position_title,
          total: test.total + 1,
          pending:
            item.status === ClaimStatus.PENDING
              ? test.pending + 1
              : test.pending,
          validated:
            item.status === ClaimStatus.EVALUATED
              ? test.validated + 1
              : test.validated,
        });
      } else {
        response.push({
          position_title: item.position_title,
          total: item.total,
          pending: item.status === ClaimStatus.PENDING ? 1 : 0,
          validated: item.status === ClaimStatus.EVALUATED ? 1 : 0,
        });
      }
    }
    return response;
  };

  GetSummary = (
    data: GetClaimsReportInterface[]
  ): {
    position_title: string;
    total: number;
    pending: number;
    validated: number;
  }[] => {
    let response: PositionSummaryInterface[] = [];
    const res_data = data.filter(
      (itm) => itm.type_key === this.state.selected_claim_type
    );
    for (const item of res_data) {
      response.push({
        position_title: item.title,
        total: 1,
        status: item.claims.filter(
          (itm) => itm.level === this.state.selected_level
        )[0].claim_status,
      });
    }
    return this.GetSummaryFilter(response).sort((a, b) =>
      a.pending < b.pending ? 1 : b.pending < a.pending ? -1 : 0
    );
  };

  GetSummaryCounts = (
    data: {
      position_title: string;
      total: number;
      pending: number;
      validated: number;
    }[]
  ): {
    pending: number;
    validated: number;
    total: number;
  } => {
    let res: {
      pending: number;
      validated: number;
      total: number;
    } = {
      pending: 0,
      validated: 0,
      total: 0,
    };
    let pending: number = 0;
    let validated: number = 0;
    let total: number = 0;
    for (const item of data) {
      pending = pending + item.pending;
      validated = validated + item.validated;
      total = total + item.total;
    }
    res = {
      pending: pending,
      validated: validated,
      total: total,
    };
    return res;
  };

  componentDidMount = () => {
    if (this.props.schoolLevels.selectedEducationCategory === null) {
      if (this.props.schoolLevels.education_categories !== null) {
        const test = this.props.schoolLevels.education_categories.find(
          (itm) =>
            itm.education_institute_id ===
            this.props.auth.education_institution_id
        );
        if (test !== undefined) {
          this.props.setSelectedEducationCategory(test);
          if (test.session_plan !== null) {
            this.GetClaims(this.state.selected_level, test.session_plan._id);
          }
        }
      }
    } else {
      if (
        this.props.schoolLevels.selectedEducationCategory.session_plan !== null
      ) {
        this.GetClaims(
          this.state.selected_level,
          this.props.schoolLevels.selectedEducationCategory.session_plan._id
        );
      }
    }
  };
  render() {
    const selected_institution =
      this.props.schoolLevels.education_categories?.find(
        (itm) =>
          itm.education_institute_id ===
          this.props.auth.education_institution_id
      );
    if (selected_institution === undefined) {
      return (
        <div>
          <div className="rounded-xl border-2 border-gray-600 flex flex-col items-center justify-center p-7 text-center w-full mt-5 bg-white">
            <span className="font-extrabold text-3xl text-gray-600">
              Your institute is not yet defined
            </span>
            <span>
              If you have an institute assigned to you, please refresh your page
              or wait a moment... your page...
              {/* You are not allowed to view shortlisting report because you have
              no access for this report. please contact your administrator */}
            </span>
          </div>
        </div>
      );
    }
    if (
      this.state.loading === true ||
      this.props.schoolLevels.selectedEducationCategory === null ||
      this.state.data === null
    ) {
      return (
        <div className="bg-white rounded p-5 mt-5">
          <Loading />
          <div className="-mt-5 ml-5">Loading report, please wait...</div>
        </div>
      );
    }
    if (this.state.error !== "") {
      <div onClick={() => this.setState({ error: "" })}>
        <div className="cursor-pointer rounded-xl border-2 border-yellow-600 flex flex-col items-center justify-center p-7 text-center w-full mt-5 bg-yellow-100">
          <span className="font-extrabold text-3xl text-yellow-800">
            Error occurred!
          </span>
          <span>{this.state.error}</span>
        </div>
      </div>;
    }
    return (
      <div>
        <div className="mt-4 bg-white rounded">
          <div className="p-4 flex flex-row items-center gap-3 justify-between">
            <div>
              <div className="text-3xl font-extrabold">
                Claiming report (
                <span className="text-blue-600 lowercase">
                  {capitalizeFirstLetter(
                    this.state.selected_claim_type.toString()
                  )}
                </span>
                )
              </div>
              <div className="flex flex-row items-center gap-2 ml-2">
                <div className="flex flex-col">
                  <span className="text-sm font-bold">Choose claim type</span>
                  <select
                    onChange={(e) => {
                      const selected_type = e.target.value as PlansTypeKeys;
                      this.setState({
                        selected_claim_type: selected_type,
                      });
                    }}
                    value={this.state.selected_claim_type}
                    className="px-3 py-2 rounded border border-gray-500"
                  >
                    <option value={PlansTypeKeys.APPLICATION}>
                      Applications
                    </option>
                    <option value={PlansTypeKeys.DOCUMENT_VALIDATION}>
                      Document validation
                    </option>
                    <option value={PlansTypeKeys.EXAMINATION}>
                      Examinations
                    </option>
                  </select>
                </div>
                <div className="flex flex-col">
                  <span className="text-sm font-bold">Choose claim level</span>
                  <select
                    value={this.state.selected_level}
                    onChange={(e) => {
                      if (selected_institution.session_plan !== null) {
                        const selected_level = parseInt(e.target.value);
                        this.setState({
                          selected_level: selected_level,
                        });
                        this.setLoading(true);
                        this.GetClaims(
                          selected_level,
                          selected_institution.session_plan._id
                        );
                      }
                    }}
                    className="px-3 py-2 rounded border border-gray-500"
                  >
                    <option value={1}>Level 1</option>
                    <option value={2}>Public Service Commission Level</option>
                  </select>
                </div>
              </div>
            </div>
            <div className="flex flex-row items-center gap-3">
              <div className="flex flex-col rounded bg-blue-100 p-2 text-right">
                <span className="text-3xl font-extrabold">
                  {displayNumbers(
                    this.GetSummaryCounts(this.GetSummary(this.state.data))
                      .total
                  )}
                </span>
                <span className="font-normal text-sm text-blue-900">
                  Total claims
                </span>
              </div>
              <div className="flex flex-col rounded bg-blue-100 p-2 text-right">
                <span className="text-3xl font-extrabold">
                  {displayNumbers(
                    this.GetSummaryCounts(this.GetSummary(this.state.data))
                      .pending
                  )}
                </span>
                <span className="font-normal text-sm text-blue-900">
                  Waiting claims
                </span>
              </div>
              <div className="flex flex-col rounded bg-green-100 p-2 text-right">
                <span className="text-3xl font-extrabold text-green-900">
                  {displayNumbers(
                    this.GetSummaryCounts(this.GetSummary(this.state.data))
                      .validated
                  )}
                </span>
                <span className="font-normal text-sm text-green-900">
                  Validated claims
                </span>
              </div>
            </div>
          </div>
          <div className="border-t border-gray-400 p-4">
            <div className="flex flex-row items-center rounded mb-3">
              <div
                onClick={() => this.setState({ view_type: "Table" })}
                className={`px-3 py-2 ${
                  this.state.view_type === "Table"
                    ? "bg-white font-bold text-blue-800 border-b-4 border-blue-600 rounded-t-md animate__animated animate__fadeIn"
                    : "bg-white font-light  border-b-4 border-gray-200 hover:bg-blue-100 hover:text-blue-800 hover:border-blue-600"
                } cursor-pointer`}
              >
                Table view
              </div>
              <div
                onClick={() => this.setState({ view_type: "Analytical" })}
                className={`px-3 py-2 ${
                  this.state.view_type === "Analytical"
                    ? "bg-white font-bold text-blue-800 border-b-4 border-blue-600 rounded-t-md animate__animated animate__fadeIn"
                    : "bg-white font-light  border-b-4 border-gray-200 hover:bg-blue-100 hover:text-blue-800 hover:border-blue-600"
                } cursor-pointer`}
              >
                Analytical view
              </div>
            </div>
            {this.state.view_type === "Table" ? (
              <table className="w-full animate__animated animate__fadeIn">
                <thead className="bg-blue-100 ">
                  <tr className="">
                    <th className="px-3 py-1 text-center">#</th>
                    <th className="px-3 py-1 text-left">Position</th>
                    <th className="px-3 py-1 text-center">Total claims</th>
                    <th className="px-3 py-1 text-center">Waiting claims</th>
                    <th className="px-3 py-1 text-center">Validated claims</th>
                  </tr>
                </thead>
                <tbody>
                  {this.GetSummary(this.state.data).length === 0 ? (
                    <tr>
                      <td
                        colSpan={5}
                        className="text-center p-3 text-3xl font-light bg-gray-100 border border-gray-500 rounded"
                      >
                        No result found!
                      </td>
                    </tr>
                  ) : (
                    this.GetSummary(this.state.data).map((item, i) => (
                      <tr
                        key={i + 1}
                        className={`${
                          i % 2 !== 0 ? "bg-gray-100" : "bg-white"
                        } ${
                          item.total === item.validated
                            ? "text-green-900 font-light"
                            : item.validated === 0
                            ? "text-yellow-900 font-bold"
                            : "text-black font-light"
                        }`}
                      >
                        <td className="px-3 py-1 text-center">{i + 1}</td>
                        <td className="px-3 py-1 text-left">
                          {item.position_title}
                        </td>
                        <td className="px-3 py-1 text-center">
                          {displayNumbers(item.total)}
                        </td>
                        <td className="px-3 py-1 text-center">
                          {displayNumbers(item.pending)}
                        </td>
                        <td className="px-3 py-1 text-center">
                          {displayNumbers(item.validated)}
                        </td>
                      </tr>
                    ))
                  )}
                </tbody>
              </table>
            ) : (
              <ClaimReportAnalytic
                general_summary={this.GetSummaryCounts(
                  this.GetSummary(this.state.data)
                )}
                summary_details={this.GetSummary(this.state.data)}
              />
            )}
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  system,
  positions,
  schoolLevels,
}: StoreState): {
  auth: Auth;
  system: System;
  positions: positionsStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
} => {
  return {
    auth: auth,
    system: system,
    positions: positions,
    schoolLevels,
  };
};

export const ClaimReport = connect(mapStateToProps, {
  setSelectedEducationCategory,
  loadAllPositionsByInstitute,
})(_ClaimReport);
